  .menu_wrapper {
    margin-top: 56px;
    width: 100%;
    overflow-x: scroll;
    padding: 0;
}
  
  .table_menu_list {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    background: rgba(245, 222, 179, 0);
    height: 46px;
    white-space: nowrap;
    margin: 0;
    padding: 0;
    justify-content: flex-start;
}
  /* Optional styles to add some space between menu items */
  .table_menu_list > * {
    margin-right: 10px;
  }


  
  .menu_wrapper::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  
  /* Firefox */
  .menu_wrapper::-moz-scrollbar {
    width: 0;
    height: 0;
  }
