}
.incdecbtn {
  width: 125px;
  height: 30px;
  background-color: #2E7D32;
  border-radius: 2rem;
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: x-large;
}
#decBtn , #incBtn{
  cursor: pointer;
}
#dishdescription {
  width: 700px;
}
#calorimg {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.table {
  animation: fade-up 0.5s ease-in-out;
}
tr{
  animation: fade-up 0.5s ease-in-out;

}
@keyframes fade-up {
  from {
    transform: translateY(70px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}





@media (max-width:970px){
  #dishdescription {

    width:auto;
  }

}
@media (max-width:600px){
  #calorimg {
    display: flex;
    flex-direction: column;
}

}

